// File for scss-vars.
// For example: main colors, paddings and so on.

//COLORS
$main: #01e2ff;
$purple: #ed145b;
$white: white;
$black: #5a5a5a;
$grey: #f4f4f4;
$blue: #93aac9;
$darkblue: #1d1f2a;

@font-face{
    font-family: Thin;
    src: local("Thin"), url(../fonts/SF-UI-Display-Thin.ttf)
}
@font-face{
    font-family: Bold;
    src: local("Bold"), url(../fonts/SF-UI-Display-Bold.ttf)
}
@font-face{
    font-family: SF-Black;
    src: local("SF-Black"), url(../fonts/SF-UI-Display-Black.ttf)
}
@font-face{
    font-family: SF-Regular;
    src: local("Regular"), url(../fonts/SF-UI-Display-Regular.ttf)
}

$Thin: Thin, sans-serif;
$Bold: Bold, sans-serif;
$Regular: SF-Regular, sans-serif;
$SF-Black: SF-Black, sans-serif;


$transition: all linear 0.3s;
