// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;
    text-justify: newspaper;
}

// You can add your own mixins here:


$break-small: 766px;
$break-large: 767px;
$break-large-right: 1300px;
$only-desk: 1200px;
$break-large-desktop:1600px;
@mixin respond-to($media) {
    @if $media == mobile {
        @media only screen and (max-width: $break-small) {
            @content;
        }
    } @else if $media == tablet {
        @media only screen and (min-width: $break-large) and (max-width: $break-large-right - 1) {
            @content;
        }
    } @else if $media == t-p {
        @media only screen and (min-width: $break-large) and (max-width: $break-large-right) and (orientation: portrait) {
            @content;
        }
    } @else if $media == t-and-m {
        @media only screen and (max-width: $break-large-right) {
            @content;
        }

    }@else if $media == d-l {
        @media only screen and (min-width: $break-large-desktop) {
            @content;
        }
    }@else if $media == from-desktop {
        @media only screen and (min-width: $only-desk) {
            @content;
        }

    }@else if $media == t-and-d {
        @media only screen and (min-width: $break-large) {
            @content;
        }
    }
}
