// Common styles of the project





h1 {

}

.clearfix {
    overflow: auto;
    zoom: 1;
}
body{
    font-family: $Regular;

}
h1{
    font-size: 57px;
    color: white;
    font-family: $Thin;
}

h2{
    font-size: 40px;
    color: black;
    font-family: $Thin;

}
@media (max-width: 768px) {
    h1{
        font-size: 40px;
    }
    h2{
        font-size: 30px;
    }

}

/*
  ====================================================================
  Header
  ====================================================================
*/

.main-header{
    min-height: 750px;
    background: url("../img/content/background_header.png") no-repeat;
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    &__logo{
        padding: 60px 0 70px 15px;
        position: relative;
    }
    &__title{
        margin: 0 auto;
        text-align: center;
        position: relative;
        h1{
            position: relative;
        }

        span{
            margin-top: 30px;
            font-family: $Regular;
            line-height: 80px;
            position: relative;
        }
    }
    .register__btn{
        margin: 0 auto;
        padding: 50px 0;
    }
    &__h3{
        color: $white;
        text-transform: uppercase;
        line-height: 25px;
        position: relative;
    }
}

@media (max-width: 768px) {
    .main-header {
        min-height: 750px;
        &__h3{
            padding: 0 15px;
        }
    }
}
@media (max-width: 600px) {
    .main-header {
        min-height: 750px;
        &__title {
            span {
                line-height: 40px;
            }
        }
    }
}
/*
  ====================================================================
  Main
  ====================================================================
*/

.notebook{
    min-height: 400px;
    background: $grey;
    &__item{
        float: left;
        width: 50%;
        position: relative;
    }
    &__title{
        padding-top: 50px;
        height: 400px;
        &:before {
            content: '';
            position: absolute;
            top: 110px;
            width: 129px;
            height: 2px;
            background-color: $purple;
        }
    }
    ul{
        padding-top: 50px;
    }
    li{
        line-height: 80px;
        background: url("../img/content/inteligentny.png") no-repeat;
        width: 280px;
        height: 100px;
        padding-left: 130px;
        &:nth-child(1){
            background-position: 15px 0px;
        }
        &:nth-child(2){
            background-position: 15px -140px;
        }
        &:nth-child(3){
            background-position: 15px -280px;
        }
    }
    img{
        width: 725px;
        position: relative;
        top: 30px;
        float: right;
    }
}
@media (max-width: 1024px) {
    .notebook{
        min-height: 200px;
        &__item{
            float: none;
            width: 100%;
        }
        &__title{
            margin: 0 auto;
            text-align: center;
            &:before{
                position: static;
            }
        }
        img{
            display: none;
        }
        li{
            margin: 0 auto;
        }
    }
}

@media (max-width: 600px) {
    .notebook{
        min-height: 300px;
        ul{
            margin: 0 auto;
        }
        p{
            font-size: 13px;
        }
        li{
            padding-left: 40px;
        }
    }
}
.scheme{
    padding-top: 50px;
    min-height: 330px;
    background: $white;
    &__title{
        margin: 0 auto;
        text-align: center;
    }
    li {
        margin-top: -50px;
        float: left;
        width: 25%;
        text-align: center;
    }
    &__image{
        background: url("../img/content/input.png") no-repeat;
        width: 75px;
        height: 100px;
        position: relative;
        top: 120px;
        left: 88px;
    }
    .image1{
        background-position: 0 0px;
    }
    .image2{
        background-position: 0 -190px;
    }
    .image3{
        background-position: 0 -365px;
    }
    .image4{
        background-position: 0 -535px;
    }
    &__arrow{
        width: 280px;
        height: 100px;
        position: relative;
        top: -15px;
    }
    .arrow_down{
        background: url("../img/content/arrow_down.png") no-repeat;
        left: 190px;
    }
    .arrow_up{
        background: url("../img/content/arrow_up.png") no-repeat;
        left: 190px;
        top: 100px;
    }

    h3{
        padding-top: 40px;
    }
}

@media (max-width: 1100px) {
    .scheme {
        min-height: 550px;
        li {
            float: none;
            margin: 0 auto;
            width: 39%;
            height: 100px;
        }
        &__image {
            top: 55px;
            left: 0px
        }
        h3{
            padding-top: 0px;
        }
        &__arrow {
            display: none;
        }

    }
}
@media (max-width: 768px) {
    .scheme {
        li {
            width: 46%;
        }
    }
}
@media (max-width: 650px) {
    .scheme {
        li {
            width: 60%;
        }
    }
}
@media (max-width: 450px) {
    .scheme {
        li {
            width: 90%;
        }
        h3{
            font-size: 13px;
        }
    }
}

.city{
    padding-top: 50px;
    min-height: 490px;
    background: $grey;
    position: relative;
    z-index: 1;
    .container{
        width: 1240px;
    }
    &__item{
        width: 50%;
        float: left;
    }
    img{
        width: 100%;
    }
    &__title{
        padding: 50px 0 0 70px;
        height: 400px;
        &:before {
            content: '';
            position: absolute;
            top: 120px;
            width: 129px;
            height: 3px;
            background-color: $purple;
        }
    }

    ul{
        padding: 70px 0 50px 0;
    }
    li{
        color: $blue;
        line-height: 30px;
        background: url("../img/content/checked.png") no-repeat;
    }
    h3{
        position: relative;
        left: 32px;
        top: -6px;
        font-size: 18px
    }
}
@media (max-width: 1100px) {
    .city {
        background: url("../img/content/london.png") no-repeat;
        background-position: center center;
        background-size: cover;
        background-attachment: fixed;
        min-height: 530px;
        .container{
            width: 100%;
        }
        &__item {
            width: 100%;
            float: none;
        }
        ul{
            width: 90%;
        }
        img {
            display: none
        }
        h2,h3{
            color: $white;
            font-weight: 600;
            //padding-left: 10px;

        }
    }
}
@media (max-width: 600px) {
    .city__title {
        padding: 0;
        height: 400px;
        margin: 0 auto;
        width: 90%;
        &:before {
            top: 70px;
        }
    }

}
.reviews {
    min-height: 900px;
    background: url("../img/content/background_footer.png") no-repeat;
    background-position: center center;
    background-attachment: fixed;
    position: relative;
    z-index: 2;
    &__title {
        padding-top: 100px;
    }
    h2{
        color: $white;
        text-align: center;
    }
    &__h3 {
        padding-top: 50px;
        color: $white;
        text-align: center;
        width: 730px;
        margin: 0 auto;
        font-style: italic;
        line-height: 20px;
    }
}

.form{
    background: $white;
    height: 400px;
    width: 760px;
    margin: 0 auto;
    border-radius: 20px;
    //padding: 30px;
    h2{
        color: $black;
        padding: 30px 0 40px 0;
    }
    p{
        line-height: 41px;
        font-size: 14px;
    }
    &__list{
        margin: 0 auto;
        width: 580px;
    }
    &__item{
        float: left;
        width: 290px;
        //padding: 30px;
    }
    ul{
        height: 100px;
    }
    li{
        float: left;
        height: 20px;
        padding: 0 5px;
    }
    input{
        width: 270px;
        height: 25px;
        border-radius: 15px;
        border: 1px solid $black;
        background: $grey;
        text-align: center;

        &:focus {
            outline: none;
        }
    }
    select{
        width: 70px;
        height: 30px;
        border-radius: 20px;
        border: 1px solid $black;
        background: $grey;
        &:focus {
            outline: none;
        }
    }
    .form_language{
        width: 80px;
    }
    .form-btn{
        border-radius: 30px;
        color: $white;
        padding: 10px;
        background: $purple;
        border: 3px solid $purple;
        transition: $transition;
        margin-top: 35px;

        &:hover{
            background: transparent;
            border: 3px solid $purple;
            transition: $transition;
            color: purple;
        }
        &:focus {
            outline: none;
        }
    }
    .message {
        padding: 10px;
        margin-bottom: 20px;
        background-color: #efa0a0;
        color: #cc5555;
        text-align: center;
        font-size: 14px;
        &:empty {
            display: none;
        }
    }
    .error{
        border: 1px solid $purple;
    }
    label.error {
        width: 0px;
        padding-left: 10px;
        font-size: 0px;
        border: none;
        color: $purple;
        position: absolute;
        z-index: 1;
        display: none;
    }
}

@media (max-width: 767px) {
    .reviews {
        min-height: 1000px;
        &__title {
            padding-top: 50px;
        }
        &__h3 {

            width: 90%;
        }
    }
    .form {
        width: 100%;
        height: 600px;
        border-radius: 0;
        &__list{
            width: 100%;
        }
        &__item {
            float: none;
            margin: 0 auto;
        }
        h2{
            padding: 60px 0 10px 0;
        }
        ul {
            height: 75px;
        }
    }
}

/*
  ====================================================================
  Footer
  ====================================================================
*/

.main-footer{
    min-height: 200px;
    background: $darkblue;
    border-top: 1px solid $purple;
    ul{
        padding-top: 70px;
        margin: 0 auto;
    }
    li{
        float: left;
        margin: 0 auto;
        background: url("../img/content/payments.png") no-repeat;
        height: 50px;
        transition: $transition;
        cursor: pointer;
        filter: grayscale(100%);
        &:hover {
            transition: $transition;
            filter: grayscale(0);
        }
    }
    .pay1{
        background-position: 0 -146px;
        width: 80px;
    }
    .pay2{
        background-position: 0 -263px;
        width: 109px;
    }
    .pay3{
        background-position: 0 -377px;
        width: 147px;

    }
    .pay4{
        background-position: 0 -494px;
        width: 99px;
    }
    .pay5{
        background-position: 0 -624px;
        width: 170px;

    }
    .pay6{
        background-position: 0 -761px;
        width: 109px;

    }
    .pay7{
        background-position: 0 -895px;
        width: 90px;

    }
    .pay8{
        background-position: 0 -1027px;
        width: 150px;
    }
    .pay9{
        background-position: 0 -0px;
        width: 109px;
    }
    p{
        padding: 50px 0;
        color: $blue;
        text-align: justify;
        filter: grayscale(100%);
        font-size: 13px;
        &:hover {
            transition: $transition;
            filter: grayscale(0);
        }
    }
}
@media (max-width: 1100px) {
    .main-footer{
        min-height: 200px;
        ul{
            padding-left: 10%;
        }
        li {
            width: 33% !important;
        }
        p{
            width: 90%;
            margin: 0 auto;
        }

    }
}
@media (max-width: 650px) {
    .main-footer{
        min-height: 400px;
        ul{
            padding-left: 3%;
        }
        li {
            width: 50% !important;
        }
        p{
            width: 90%;
            margin: 0 auto;
        }

    }
}
