.register__btn {
  width: 200px;
  position: relative;
}

.register__btn .btn {
  text-decoration: none;
  width: 100%;
  min-height: 20px;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Roboto-Black',sans-serif;
  font-size: 13px;
  line-height: 40px;
  margin-bottom: 20px;
  position: relative;
  color: white;
  z-index: 2;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px transparent;
  transition-property: color;
  transition-duration: 0.3s;
  background-color: #ed145b;
  border: 4px solid #ed145b;
  border-radius: 30px;
}

.register__btn .btn:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 30px;
  background: white;
  transform: scaleY(0);
  transform-origin: 50%;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.register__btn .btn:hover:before, .register__btn .btn:focus:before, .register__btn .btn:active:before {
  transform: scaleY(1);
}

.register__btn .btn:hover, .register__btn .btn:focus, .register__btn .btn:active {
  color: #ed145b;
  border: 4px solid #ed145b;
}

/** RESET AND LAYOUT
===================================*/
.bx-wrapper {
  position: relative;
  /*margin: 0 auto 60px;*/
  padding: 0;
  *zoom: 1;
}

/** THEME
===================================*/
.bx-wrapper .bx-viewport {
  transform: translatez(0);
}

.bx-wrapper .bx-controls-auto {
  position: absolute;
  bottom: -30px;
  width: 100%;
  height: 100%;
}

.bx-pager-item a {
  color: transparent;
}

/* DIRECTION CONTROLS (NEXT / PREV) */
.bx-wrapper .bx-prev {
  left: 100px;
  background: url(../img/content/svg/arrow_slider.svg) no-repeat;
  transform: rotate(180deg);
}

.bx-wrapper .bx-next {
  right: 100px;
  background: url(../img/content/svg/arrow_slider.svg) no-repeat;
}

.bx-wrapper .bx-controls-direction a {
  position: absolute;
  top: 40%;
  outline: 0;
  width: 30px;
  height: 30px;
  text-indent: -50px;
  z-index: 9999;
  color: transparent;
}

.bx-wrapper .bx-controls-direction a.disabled {
  display: none;
}

@media (max-width: 1000px) {
  .bx-wrapper .bx-prev {
    display: none;
  }
  .bx-wrapper .bx-next {
    display: none;
  }
}

.clearfix {
  overflow: auto;
  zoom: 1;
}

