}

.form h2 {
  color: #5a5a5a;
  padding: 30px 0 40px 0;
}

.form p {
  line-height: 41px;
  font-size: 14px;
}

.form__list {
  margin: 0 auto;
  width: 580px;
}

.form__item {
  float: left;
  width: 290px;
}

.form ul {
  height: 100px;
}

.form li {
  float: left;
  height: 20px;
  padding: 0 5px;
}

.form input {
  width: 270px;
  height: 25px;
  border-radius: 15px;
  border: 1px solid #5a5a5a;
  background: #f4f4f4;
  text-align: center;
}

.form input:focus {
  outline: none;
}

.form select {
  width: 70px;
  height: 30px;
  border-radius: 20px;
  border: 1px solid #5a5a5a;
  background: #f4f4f4;
}

.form select:focus {
  outline: none;
}

.form .form_language {
  width: 80px;
}

.form .form-btn {
  border-radius: 30px;
  color: white;
  padding: 10px;
  background: #ed145b;
  border: 3px solid #ed145b;
  transition: all linear 0.3s;
  margin-top: 35px;
}

.form .form-btn:hover {
  background: transparent;
  border: 3px solid #ed145b;
  transition: all linear 0.3s;
  color: purple;
}

.form .form-btn:focus {
  outline: none;
}

.form .message {
  padding: 10px;
  margin-bottom: 20px;
  background-color: #efa0a0;
